.hero {
  padding: 100px 0;
  position: relative;
}
.hero__title {
  font-weight: bold;
  font-size: 72px;
  line-height: 140%;
  max-width: 760px;
  color: #333;
  margin-bottom: 50px;
}
.hero__title::after {
  position: absolute;
  content: "";
  top: 150px;
  right: 0;
  width: 400px;
  height: 2px;
  background: #000;
}
.hero__title_smaller {
  font-size: 52px;
}
.hero .hero__image {
  width: 100%;
  height: 600px;
}
.hero .hero__parametrs {
  position: absolute;
  right: 140px;
  top: 240px;
  background: #2C3878;
  width: 252px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding-top: 30px;
}
.hero .hero__parametrs__item {
  padding: 40px 0;
}
.hero .hero__parametrs__item span {
  display: block;
  color: #fff;
  font-weight: 400;
  font-size: 16px;
  padding-bottom: 10px;
  line-height: 160%;
}
.hero .hero__parametrs__item strong {
  font-size: 48px;
}